<template lang="pug">
  .top-players#top-players
    .top-players-wrapper
      .container
        .container-header.flex.justify-center.h-full
          .header-text.h-full.flex.items-center
            p Топ игроков
        .container-body
          .table-
            .line.flex
              p.number #
              p.user Игрок
              p.played Сыграно
              p.wins Выиграно
              p.ratio W/L
              p.sum Сумма выигрыша
            template(v-for="(topPlayer, index) in topPlayers")
              .divider
              .line.flex.items-center
                p.number.value {{ index + 1 }}.
                r-user(v-bind="topPlayer.user" :grayName="true")
                p.played {{ topPlayer.played }} дуэлей
                p.wins {{ topPlayer.wins }} дуэлей
                p.ratio.value {{ topPlayer.ratio }}
                p.sum &#8381; 
                  span.sum.value {{ topPlayer.sum }}
            .divider
          
        .more-button.flex.justify-center.cursor-pointer(
          @click="fillPlayers"
        )
          r-svg(
            name="arrow"
            width="12px"
            )

</template>

<script>
export default {
  name: 'TopPlayers',
  mounted() {
    this.fillPlayers();
  },
  data: () => ({
    topPlayers: []
  }),
  methods: {
    fillPlayers() {
      for (let i = 0; i < 10; i++) {
        this.topPlayers.push({
          user: {
            id: 'asd',
            name: 'makerOfMoney',
            avatar: 'https://i.stack.imgur.com/RIZKi.png?s=64&g=1',
          },
          played: 910,
          wins: 902,
          ratio: 1.23,
          sum: '39 500.00'
        })
      }
    },
  }
}
</script>

<style lang="scss" scoped>
.top-players {
  background-color: #13151c;
  padding: 0 0 300px;
  .top-players-wrapper {
    width: 70vw;
    max-width: 1400px;
    min-width: 1000px;
    margin: 0 auto;
    .container {
      width: 100%;
      background-color: #1A1D25;
      border-radius: 5px;
      .container-header {
        height: 80px;
        background: url(../../img/trophy.png);
        border-radius: 5px 5px 0 0;
        .header-text {
          font-size: 16px;
          padding: 0 15px;
          border-top: 2px solid #F73B3B ;
          background: linear-gradient(0, rgba(247, 59, 59, 0) 50.22%, #F73B3B 786.67%);
        }
      }
      .more-button {
        padding: 20px 0;
        border-radius: 0 0 5px 5px;
        &:hover {
          transition: .3s ease-in-out;
          background-color: #7689990f;
        }
      }
      .container-body {
        border-radius: 0 0 5px 5px;
        padding: 0 80px;
        color: #B0AFD4;
        .table- {
          .divider {
            width: 100%;
            height: 2px;
            background-color: #20232D;
          }
          .line {
            padding: 20px 0;
            .number {
              width: 42px;
              &.value {
                color: #fff !important;
              }
            }
            .user {
              width: 328px;
            }
            .played {
              width: 263px;
            }
            .wins {
              width: 261px;
            }
            .ratio {
              width: 155px;
              &.value {
                color: #fff !important;
              }
            }
            .sum {
              width: 150px;
              &.value {
                color: #4fdd1d !important;
              }
            }
          }
        }
      }
    }

  }
}
</style>