<template lang="pug">
  .header(
    :style="`background-color: ${isDark ? '#1a1d25' : ''}; transition: .3s ease-in-out;`"
  )
    .header-wrapper
      .flex.justify-between.items-center
        r-logo(
          weight="bold"
          size="26px"
        )
        .flex
          template(v-for="link in links")
            a.link(@click="test(link.to)")
              p {{ link.name }}
        .steam.flex.items-center
          r-svg(
            name="steam"
            width="24px"
          )
          p.steam-text Войти через STEAM
</template>
<script>
export default {
  name: 'TheHeader',
  mounted() {
    window.addEventListener('scroll', () => {
      this.isDark = window.scrollY > 100;
    })
  },
  data: () => ({
    isDark: false,
    links: [{
      name: 'Live лента',
      to: '#live',
    },{
      name: 'О нас',
      to: '#about',
    },{
      name: 'Новости',
      to: '#news',
    },{
      name: 'Топ игроков',
      to: '#top-players',
    },{
      name: 'Отзывы',
      to: '#reviews',
    },]
  }),
  methods: {
    test(id) {
      const n = document.getElementById(id.replace('#', '')).getBoundingClientRect().top - 160;
      window.scrollBy({ top: n, behavior: "smooth" })
    }
  }
}
</script>
<style lang="scss" scoped>
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 62px;
  z-index: 999;
  .header-wrapper {
    position: absolute;
    padding: 8px 0;
    width: 70vw;
    left: 0;
    right: 0;
    z-index: 999;
    max-width: 1400px;
    min-width: 1000px;
    margin: 0 auto;
    .link {
      padding: 0 25px;
      &:hover {
        p {
          color: #fdfdfd !important;
          transition: .3s ease-in-out;
        }
      }
      p {
        color: #B0AFD5;
        font-size: 16px;
        line-height: 20px;
      }
    }
    .steam {
      background-color: rgba(19, 21, 29, 0.25);
      border-radius: 5px;
      padding: 11px 20px;
      .steam-text {
        color: #B0AFD5;
        font-size: 16px;
        margin-left: 20px;
      }
      &:hover {
        cursor: pointer;
        transform: scale(1.05);
        transition: .3s ease-in-out;
        background-color: rgba(21, 24, 33, 0.45);
      }
    }
  }
}
</style>