<template lang="pug">
  .w-full.h-full
    .the-news.h-full
      .bg.h-full.w-full
        img(
          :src="avatar"
        )
      .gradient.h-full
      .content.flex.flex-col.justify-between(
        :style="`padding: ${big ? '30px 60px' : '15px 30px'}`"
      )
        p.date(
          :style="`font-size: ${big ? '16px' : '14px'}`"
        ) {{ formatDate }}
        div
          p.title(
            :style="getTitleStyle"
          ) {{ title }}
          p.subtitle {{ subtitle }}
</template>

<script>
export default {
  name: "TheNews",
  props: [
    'avatar',
    'id',
    'title',
    'subtitle',
    'date',
    'big'
  ],
  computed: {
    formatDate() {
      return moment(1631789104000).calendar();
    },
    getTitleStyle() {
      return `font-size: ${this.big ? '20px' : '18px'} !important; line-height: ${this.big ? '25px' : '22px'} !important; -webkit-line-clamp: ${this.big ? '2' : '1'} !important; margin-bottom: ${this.big ? '11px' : '3px'}; `
    },
  }
}
</script>

<style lang="scss" scoped>
.the-news {
  position: relative;
  border-radius: 5px;
  cursor: pointer;
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    img {
      object-fit: cover;
      height: 100%;
      border-radius: 5px;
      width: -webkit-fill-available;
    }
  }
  .gradient {
    background: linear-gradient(180deg, rgba(19, 21, 28, 0) 50%, #13151C 107.6%);
    border-radius: 5px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 30px 60px;
    .date {
      width: fit-content;
      color: #fdfdfd;
      background-color: #13151c;
      border-radius: 5px;
      padding: 1px 7px;
    }
    .title {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .subtitle {
      font-size: 16px;
      line-height: 20px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      -webkit-line-clamp: 2;
      color: #B0AFD4;
    }
  }
}
</style>