import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Agreement from '../views/Agreement.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Main',
    component: Main
}, {
    path: '/agreement',
    name: 'Agreement',
    component: Agreement
}, ]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router